
.weui_mask{position:fixed;z-index:1000;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0.6)
}
.weui_mask_transparent{position:fixed;z-index:5001;width:100%;height:100%;top:0;left:0
}
.weui_mask_transition{display:none;position:fixed;z-index:1000;width:100%;height:100%;top:0;left:0;background:rgba(0,0,0,0);transition:background .3s
}
.weui_fade_toggle{background:rgba(0,0,0,0.6)
}
.weui_actionsheet{position:fixed;left:0;bottom:0;transform:translate(0, 100%);-webkit-backface-visibility:hidden;backface-visibility:hidden;z-index:5000;width:100%;height:100%;transition:transform .3s
}
.weui_actionsheet_menu{background-color:#FFFFFF;position:fixed;left:0;bottom:0;transform:translate(0, 100%);-webkit-backface-visibility:hidden;backface-visibility:hidden;z-index:5000;width:100%;transition:transform .3s
}
.weui_actionsheet_action{margin-top:6px;background-color:#FFFFFF
}
.weui_actionsheet_cell{position:relative;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center;min-height:25px;padding:10px 0;text-align:center;font-size:18px;cursor:pointer
}
.weui_actionsheet_cell:before{content:" ";position:absolute;left:0;top:0;width:100%;height:1px;border-top:1px solid #D9D9D9;color:#D9D9D9;transform-origin:0 0;transform:scaleY(.5)
}
.weui_actionsheet_cell:active{background-color:#ECECEC
}
.weui_actionsheet_cell:first-child:before{display:none
}
.weui_actionsheet_toggle{transform:translate(0, 0)
}
.vux-actionsheet-gap{height:8px;width:100%;background-color:#eee
}
.vux-actionsheet-cancel:before{border-top:none
}
@media screen and (min-width:450px){
.weui_actionsheet{width:450px
}
}


